<template>
    <section id="login">
        <div class="container">
            <router-link to="/" aria-current="page">
                <img src="../assets/img/logo3.png" alt="logo1">
            </router-link>
            <h1>Login</h1>
            <h3 class="input-errors mb-3 text-center">{{ errorCode }}</h3>
            <label for="email">E-Mail</label>
            <input type="email" v-model="v$.email.$model" id="email" placeholder="Email Address">
            <div class="input-errors" v-for="(error, index) of v$.email.$errors" :key="index">
                <div class="error-msg">{{ error.$message }}</div>
            </div>

            <label for="password">Password</label>
            <input type="text" v-model="v$.password.$model" id="password" placeholder="Password">
            <div class="input-errors" v-for="(error, index) of v$.password.$errors" :key="index">
                <div class="error-msg">{{ error.$message }}</div>
            </div>

            <a @click="forgotPassword" class="input-errors text-end" style="cursor: pointer;">Forgot password</a>
            <button @click="login" type="button">Login</button>

            <router-link to="/login-with-phone" >
                <button class="button_1">
                    Login With Phone Number
                </button>
            </router-link>

            <router-link to="/signup" class="link">
                <h4>Sign Up</h4>
            </router-link>
        </div>
    </section>
</template>
  
<script>
import Toast from '@/main'
import { useVuelidate } from '@vuelidate/core'
import { required, email, helpers } from '@vuelidate/validators'

import { getAuth, signInWithEmailAndPassword, sendPasswordResetEmail } from "firebase/auth";

export default {
    name: 'login',
    setup: () => ({ v$: useVuelidate() }),
    data() {
        return {
            email: '',
            password: '',
            errorCode: '',
            auth: getAuth()
        }
    },
    validations() {
        return {
            email: {
                required: helpers.withMessage('* Please type Your Email Address.', required),
                email
            },
            password: {
                required: helpers.withMessage('* Please type Your Password.', required),
            }
        }
    },
    methods: {
        async login() {
            const isFormCorrect = await this.v$.$validate()

            if (isFormCorrect == true) {
                // actually Login form

                await signInWithEmailAndPassword(this.auth, this.email, this.password)
                    .then((userCredential) => {
                        // Signed in 
                        const user = userCredential.user;
                        localStorage.setItem("Userdata", JSON.stringify(user))
                        console.log(user, 'user');
                        Toast.fire({
                            icon: "success",
                            title: user.displayName + ", Signed in successfully",
                        });
                        this.$router.push({ path: '/' })
                    })
                    .catch((error) => {
                        this.errorCode = error.code;
                    });
            }
        },
        forgotPassword() {
            sendPasswordResetEmail(this.auth, this.email)
                .then(() => {
                    Toast.fire({
                        icon: "info",
                        title: "please check your email.",
                    });
                    this.$router.push({ path: '/login' })
                })
                .catch((error) => {
                    this.errorCode = error.code;
                });
        }
    },
    mounted() {
        let user = localStorage.getItem("Userdata");
        if (user) {
            this.$router.push({ path: '/' })
        }
    }
}
</script>

